header {
	width: 100%;
	height: auto;
	position: absolute;
	z-index: 100;
	padding: 0 5%;
	box-sizing: border-box;
	display: flex;
	background: none;
}

.h-center {
	max-width: 1700px;
	min-width: 1500px;
}

header h1 {
	width: 100px;
	margin: auto;
	height: 60px;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	text-align: center;
	z-index: 1;
}

header h1 a {
	width: 100px;
	margin: 0 auto;
}

header h1 a img {
	width: 100px;
	height: 60px;
}

header .main-nav {
	width: 100%;
	height: 60px;
}

header .main-nav .main-ul {
	display: flex;
	justify-content: flex-start;
	flex-flow: row nowrap;
}

header .main-nav .main-ul li {
	width: 120px;
	text-align: center;
	line-height: 58px;
}

header .main-nav .main-ul li a {
	color: white;
	font-size: 20px;
	font-weight: 600;
}

header .main-nav .main-ul li .subnav-wrap {
	position: absolute;
	top: 60px;
	left: 0;
	padding: 0 5%;
	box-sizing: border-box;
	width: 100%;
	height: 400px;
	background-color: #fff;
	z-index: 10;
	display: none;
}

header .main-nav .main-ul li .subnav-wrap .subnav-top {
	width: 100%;
	height: 340px;
}

header .main-nav .main-ul li .subnav-wrap .subnav-top .sub-ul {
	width: 100%;
	display: flex;
}

header .main-nav .main-ul li .subnav-wrap .subnav-top .sub-ul li {
	margin-top: 30px;
	width: auto;
	padding: 0 20px;
	box-sizing: border-box;
}

header .main-nav .main-ul li .subnav-wrap .subnav-top .sub-ul li a {
	color: black;
	font-size: 18px;
}

header .main-nav .main-ul li .subnav-wrap .subnav-top .sub-ul .mobil-ul {
	width: 100%;
	height: auto;
	display: flex;
	flex-flow: column wrap;
}
header .main-nav .main-ul li .subnav-wrap .subnav-top .sub-ul .mobil-ul li {
	width: 100%;
	height: 15px;
}
header .main-nav .main-ul li .subnav-wrap .subnav-top .sub-ul .mobil-ul li a {
	color: #333;
	font-weight: 400;
}

header .main-nav .main-ul li .subnav-wrap .subnav-bottom {
	width: 100%;
	height: 60px;
}

header .main-nav .main-ul li .subnav-wrap .subnav-bottom .sub-ul-02 {
	width: 100%;
	width: auto;
	display: flex;
	height: 60px;
}

header .main-nav .main-ul li .subnav-wrap .subnav-bottom .sub-ul-02 li {
	padding: 0 20px;
	width: auto;
}
header .main-nav .main-ul li .subnav-wrap .subnav-bottom .sub-ul-02 li a {
	font-weight: 400;
	font-size: 16px;
	color: #000;
}
.subnav-wrap.open {
	display: block !important;
}
.open-color {
	background-color: rgb(0, 4, 31) !important;
}

.subnav-wrap-02.open {
	display: block !important;
}

/* 모바일 */
.m-header {
	width: 100%;
	height: 40px;
	background-color: rgb(0, 4, 31);
	display: flex;
	justify-content: space-between;
	position: relative;
}

.m-header h1 {
	width: 100px;
	height: 100%;
}

.m-header h1 a {
	width: 100%;
	height: 100%;
}

.m-header h1 a img {
	width: 100%;
	height: 100%;
}

.m-header .bar {
	width: 35px;
	height: 100%;
	position: relative;
	margin-right: 10px;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	cursor: pointer;
}

.m-header .bar .t-bar {
	width: 25px;
	height: 0;
	border: 1px solid #fff;
	margin: 5px 0;
}

.m-header .bar .b-bar {
	width: 25px;
	height: 0;
	border: 1px solid #fff;
	margin: 5px 0;
}
.m-header nav {
	display: none;
	width: 100%;
	height: 100vh;
	position: absolute;
	background-color: #fff;
	z-index: 10;
	overflow: hidden;
}

.m-header nav .x {
	width: 40px;
	height: 40px;
	line-height: 40px;
	text-align: right;
	font-weight: 600;
	cursor: pointer;
}

.m-header nav .x .t-bar {
	width: 25px;
	transform: rotate(45deg);
	border: 1px solid #000;
	position: absolute;
	right: 10px;
	top: 20px;
}
.m-header nav .x .b-bar {
	width: 25px;
	transform: rotate(135deg);
	border: 1px solid #000;
	position: absolute;
	right: 10px;
	top: 20px;
}

.m-header nav .login-div {
	width: 100%;
	height: 60px;
	background-color: rgb(0, 4, 31);
	position: absolute;
	bottom: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}
.m-header nav .login-div p {
	color: #fff;
}
.m-header nav .login-div a {
	color: #fff;
	padding: 0 30px;
	box-sizing: border-box;
}

.m-header nav .m-menu {
	padding-top: 60px;
	width: 100%;
	box-sizing: border-box;
}

.m-header nav .m-menu li {
	width: 100%;
	line-height: 50px;
	padding-left: 20px;
	box-sizing: border-box;
}

.m-header nav .m-menu li a {
	width: auto;
	font-weight: 600;
	font-size: 20px;
	position: relative;
	border: none;
	transition: all 0.3s;
}

.m-nav.open {
	display: block;
}

li a.under-line {
	border-bottom: 2px solid #fff;
}
