@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap");

body,
html {
	width: 100%;
	height: 100vh;
	position: relative;
}

* {
	margin: 0;
	padding: 0;
	font-family: "Noto Sans KR", sans-serif;
	font-optical-sizing: auto;
	font-style: normal;
}

ul,
ol {
	list-style: none;
}

a {
	text-decoration: none;
	display: block;
	cursor: pointer;
	color: #333;
}

#wrap {
	width: 100%;
	height: 100%;
}
.center {
	width: 90%;
	margin: 0 auto;
	min-width: 1500px;
	max-width: 1700px;
}
