.wrap {
	width: 100%;
}

.header {
	width: 100%;
	height: 60px;
	background-color: rgb(0, 4, 31);
}

.center .car-ul {
	margin-top: 100px;
	display: flex;
	width: 500px;
	justify-content: space-between;
}

.center .car-ul li {
	width: auto;
	text-align: left;
}

.center .car-ul li a {
	width: auto;
	color: black;
	font-size: 20px;
	font-weight: 600;
	height: 40px;
}

.center .car-ul li .car-a.under-line {
	font-weight: 800;
	border-bottom: 2px solid black;
}

.center .bar {
	width: 100%;
	height: 0;
	border: 0.6px solid black;
	margin-bottom: 20px 0;
}

.cars {
	width: 100%;
	display: flex;
	justify-content: flex-start;
	flex-flow: row wrap;
	margin: 100px 0;
}

.cars .detail {
	width: 33.33%;
	height: 450px;
	padding: 20px;
	box-sizing: border-box;
	text-align: center;
	transition: all 0.5s;
}
.cars .detail:hover {
	background-color: #ebebeb;
}

.cars .detail .img-box {
	width: 100%;
}
.cars .detail .img-box img {
	width: 100%;
	height: 100%;
}

.cars .detail .name {
	width: 100%;
	font-size: 20px;
	font-weight: 700;
}

.cars .detail .price {
	width: 100%;
	font-size: 20px;
	opacity: 0.8;
}

/* 모바일 */

.m-cars {
	width: 100%;
	height: auto;
}

.m-cars * {
	padding: 0;
	margin: 0;
}

.m-cars .car-ul {
	width: 100%;
	line-height: 40px;
	display: flex;
	justify-content: flex-start;
}

.m-cars .car-ul li {
	padding: 0 15px;
	position: relative;
}

.m-cars .car-ul li a {
	font-weight: 500;
}

.m-cars .bar {
	width: 100%;
	height: 0;
	border: 0.4px solid #333;
}

.m-cars .cars {
	width: 100%;
	padding: 30px 0;
	display: flex;
	justify-content: flex-start;
}

.m-cars .cars .detail {
	padding: 20px 0;
	width: 50%;
	height: 300px;
	box-sizing: border-box;
	position: relative;
}

.m-cars .cars .detail img {
	width: 100%;
}

.m-cars .cars .detail .price {
	font-size: 14px;
}
