.buy-counseling {
	width: 100%;
	min-width: 1500px;
	max-width: 1700px;
	height: 100%;
}

.buy-counseling .buy-wrap {
	width: 100%;
	height: 250px;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
}

.buy-counseling h1 {
	text-align: center;
	width: 100%;
	font-size: 40px;
}

.buy-counseling .buy-wrap p {
	width: 400px;
	text-align: center;
	margin: 0 auto;
}

.buy-counseling .wrap {
	width: 1000px;
	display: flex;
	justify-content: space-around;
	height: 300px;
	align-items: center;
	border: 1px solid #444;
	margin: 0 auto;
}
.buy-counseling a {
	width: 130px;
	height: 50px;
	text-align: center;
	line-height: 50px;
	margin: 50px auto;
	background-color: #000;
	color: #fff;
}

.buy-counseling .service {
	width: 100%;
	height: 200px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.buy-counseling ul {
	list-style: circle !important;
	margin-bottom: 50px;
}

.buy-counseling ul li {
	margin: 10px 0;
}
