.connect-store {
	width: 100%;
	height: auto;
}

.connect-store .bg {
	width: 100%;
	height: 600px;
	background: url("https://www.kia.com/content/dam/kwp/kr/ko/buy/kv_connect-store_pc.png") no-repeat center/cover;
	position: relative;
}

.connect-store .bg h3 {
	position: absolute;
	left: 30px;
	bottom: 50px;
	font-size: 50px;
	color: #fff;
}

.connect-store .section01 h4 {
	text-align: center;
	font-size: 40px;
	margin: 50px 0;
}

.connect-store .section01 p {
	text-align: center;
	width: 100%;
	text-emphasis: center;
	margin: 0 auto;
}

.connect-store .section01 a {
	width: 220px;
	height: 50px;
	text-align: center;
	background-color: #000;
	color: #fff;
	line-height: 50px;
	margin: 40px auto;
	font-size: 15px;
	font-weight: 500;
}

.connect-store .section02 {
	margin: 70px auto;
	width: 1400px;
	height: 500px;
	display: flex;
}
.connect-store .section02 .left {
	width: 700px;
	background: url("https://www.kia.com/content/dam/kwp/kr/ko/buy/connect-store-Img_01_pc.jpg") no-repeat center/cover;
}

.connect-store .section02 .right {
	width: 700px;
	background-color: rgba(0, 0, 0, 0.03);
	padding: 60px;
	box-sizing: border-box;
	display: flex;
	flex-flow: column;
	justify-content: center;
}

.connect-store .section02 .right h3 {
	font-size: 35px;
	margin: 10px 0;
}

.connect-store .section02 .right span {
	font-size: 20px;
	color: #333;
	margin: 10px 0;
}

.connect-store .section02 .right p {
	font-size: 19px;
	font-weight: 500;
	margin: 5px 0;
}
