.e-banner {
	width: 100%;
	height: 300px;
	padding-top: 100px;
	box-sizing: border-box;
}

.e-banner h3 {
	font-size: 40px;
	text-align: center;
}

.e-banner p {
	font-size: 20px;
	color: #555;
	text-align: center;
}

.e-box {
	width: 1200px;
	height: auto;
	margin: 50px auto;
	display: flex;
	flex-flow: row wrap;
	justify-content: flex start;
}

.e-box .e-box-detail {
	width: 50%;
	height: 500px;
	padding: 20px;
	box-sizing: border-box;
}

.e-box .e-box-detail img {
	width: 100%;
}
