footer {
	width: 100%;
	height: 250px;
	background-color: rgb(0, 4, 31);
}

footer h1 {
	width: 100%;
	height: 60px;
}
footer h1 a {
	width: 100%;
	height: 100%;
}
footer h1 a img {
	width: 100%;
	height: 100%;
}

footer div {
	display: flex;
	width: 90%;
	height: 100px;
	line-height: 100px;
	margin: 0 auto;
	justify-content: space-around;
}

footer div a {
	color: #fff;
}

footer p {
	width: 100%;
	color: white;
	text-align: center;
}

/* 모바일 */

.m-footer {
	width: 100%;
	height: 100px;
	background-color: rgb(0, 4, 31);
}

.m-footer img {
	width: 100%;
	height: 40px;
}

.m-footer p {
	width: 90%;
	margin: 0 auto;
	color: #fff;
	text-align: center;
	font-weight: 200;
}
