.cars-benefit {
	width: 100%;
	height: 100%;
}

.cars-benefit h1 {
	width: 100%;
	height: 200px;
	text-align: center;
	line-height: 200px;
}

.cars {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.cars .car1 {
	width: 250px;
}

.cars .car1 h3 {
	font-size: 30px;
}
.cars .car1 p {
	font-size: 17px;
	font-weight: 400;
}

.cars .car1 span {
	font-size: 20px;
	font-weight: 700;
}
.cars .car2 {
	width: 500px;
}

.cars .car2 .img-box {
	width: 500px;
}
.cars .car2 .img-box img {
	width: 500px;
}

.cars .car2 .btn {
	width: 500px;
	display: flex;
	justify-content: space-around;
	margin: 40px 0;
}

.cars .car2 .btn a {
	width: 140px;
	border: 1px solid #000;
	height: 45px;
	text-align: center;
	line-height: 45px;
	font-weight: 600;
}

.cars .car2 .btn a:nth-child(3) {
	background-color: #000;
	color: #fff;
}

.cars .car3 {
	width: 500px;
}

.cars .car3 .cars3-detail {
	width: 100%;
	display: flex;
	justify-content: space-between;
	margin: 20px 0;
}
.cars .car3 .cars3-detail div {
	line-height: 50px;
}
.cars .car3 .cars3-detail div:nth-child(1) {
	width: 100px;
}
.cars .car3 .cars3-detail div:nth-child(2) {
	width: 150px;
}
.cars .car3 .cars3-detail div:nth-child(3) {
	width: 150px;
}

.cars .car3 .more {
	margin: 20px 0;
}
