.swiper-wrapper {
	width: 100%;
	height: 100%;
}

.swiper-slide {
	text-align: center;
	font-size: 18px;
	/* background: #fff; */
	display: flex;
	justify-content: center;
	align-items: center;
}
.swiper-button-next::after {
	color: #333;
}
.swiper-button-prev::after {
	color: #333;
}

.swiper-pagination-bullet {
	opacity: 1 !important;
	background-color: #333 !important;
}

.swiper-pagination-bullet-active {
	background-color: #ebebeb !important;
}
.slide {
	position: relative;
	width: 100%;
	height: 100vh;
}

.slide-06 {
	background: url("https://www.kia.com/content/dam/kwp/kr/ko/main-kv-contents/202407/kv_the_new_k8_teaser_pc.jpg") no-repeat center/cover;
}

.slide-01 {
	background: url("/public/img/banner-ev3.jpg") no-repeat center/cover;
}

.slide-02 {
	background: url("/public/img/banner-ev6.jpg") no-repeat center/cover;
}
.slide-03 {
	background: url("/public/img/banner-k9.jpg") no-repeat center/cover;
}

.slide-04 {
	background: url("https://www.kia.com/content/dam/kwp/kr/ko/main-kv-contents/202311/kv_the_new_carnival_pc.jpg") no-repeat center/cover;
}

.slide-05 {
	background: url("https://www.kia.com/content/dam/kwp/kr/ko/main-kv-contents/202310/kv_used_car_pc.png") no-repeat center/cover;
}

.slide p {
	font-size: 30px;
	position: absolute;
	bottom: 200px;
	left: 100px;
	font-weight: 500;
	color: white;
	line-height: 1.8;
}

.slide h1 {
	font-size: 40px;
	position: absolute;
	bottom: 150px;
	left: 100px;
	color: white;
}

.slide a {
	color: white;
	position: absolute;
	background-color: rgb(0, 4, 31);
	width: 100px;
	height: 40px;
	text-align: center;
	line-height: 38px;
	/* border-radius: 5px; */
	left: 100px;
	bottom: 100px;
}

.slide-06 a {
	background-color: #fff;
	color: #000;
}

.best {
	width: 100%;
	height: auto;
	padding: 100px 0 0;
}

.best h1 {
	text-align: center;
	font-size: 45px;
}

.best .sort {
	margin: 30px 0 0;
	width: 100%;
	height: auto;
	display: flex;
	justify-content: center;
}

.best .sort li {
	width: auto;
	padding: 0 20px;
	line-height: 40px;
}

.best .sort li .best-link1 {
	font-weight: 500;
}

.best .sort li .best-link1.bold-weight {
	font-weight: 900;
}
.best .sort li .best-link2 {
	font-weight: 500;
}

.best .sort li .best-link2.bold-weight {
	font-weight: 900;
}
.best .sort li .best-link3 {
	font-weight: 500;
}

.best .sort li .best-link3.bold-weight {
	font-weight: 900;
}

.best .best-box {
	padding: 50px 0;
	width: 80%;
	height: auto;
	/* border: 1px solid #333; */
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}

.best .best-box .best-detail {
	/* width: 27%; */
	height: 770px;
	border: 0.9px solid #000;
	border-radius: 10px;
	padding: 30px 20px;
	position: relative;
	transition: all 0.5s;
	box-sizing: border-box;
}

.best .best-box .best-detail:hover {
	background-color: #f0f0f0;
}

.best .best-box .best-detail h1 {
	margin: 0 auto;
	font-size: 25px;
	text-align: left;
}

.best .best-box .best-detail p {
	width: 100%;
	line-height: 40px;
}

.best .best-box .best-detail img {
	width: 100%;
}

.best .best-box .best-detail ul {
	width: 100%;
	display: flex;
	flex-flow: column nowrap;
	margin: 20px 0;
}

.best .best-box .best-detail ul li {
	margin: 10px 0;
	width: 100%;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
}

.best .best-box .best-detail ul li h4 {
	width: auto;
}

.best .best-box .best-detail ul li p {
	width: auto;
	display: block;
	text-align: right;
	font-size: 16px;
	line-height: 1.8;
	color: #333;
}

.best .best-box .best-detail .bar {
	width: 90%;
	border: 0.4 solid #777;
	position: absolute;
	bottom: 160px;
}

.best .best-box .best-detail .price {
	width: 100%;
	text-align: right;
	display: block;
	color: black;
	font-size: 25px;
	font-weight: 600;
	margin: 10px 0;
	position: absolute;
	bottom: 95px;
	right: 25px;
}

.best .best-box .best-detail .price span {
	font-size: 16px;
}
.best .best-box .best-detail .more {
	width: 100%;
	position: absolute;
	bottom: 25px;
	display: flex;
	justify-content: center;
}

.best .best-box .best-detail .more a {
	font-size: 15px;
	font-weight: 600;
	line-height: 48px;
}

.best .best-box .best-detail .more a:nth-child(1) {
	width: 110px;
	height: 100px;
	text-align: center;
	height: 50px;
	border: 1px solid #333;
	box-sizing: border-box;
	margin: 0 5px;
	background-color: #fff;
}

.best .best-box .best-detail .more a:nth-child(2) {
	margin: 0 5px;
	width: 100px;
	text-align: center;
	height: 50px;
	background-color: black;
	color: #fff;
}

.best-more {
	text-align: center;
	margin-bottom: 80px;
	font-weight: 300;
}

.menu {
	width: 90%;
	margin: 0 auto;
	height: 300px;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.menu div {
	width: 23%;
	height: 200px;
	border-radius: 10px;
	text-align: center;
	transition: all 1s;
	background-color: rgb(236, 236, 236);
}

.menu div p {
	line-height: 200px;
	font-size: 17px;
	font-weight: 500;
	transition: all 1s;
}

.menu div:hover {
	background-color: rgb(0, 4, 31);
}

.menu div:hover p {
	color: white;
}

.news {
	width: 90%;
	height: 700px;
}

.news h1 {
	text-align: center;
}

.news > div {
	width: 90%;
	height: 600px;
	margin: 0 auto;
	display: flex;
	color: white;
	margin-top: 50px;
}

.news div .n-left {
	width: 50%;
	height: 450px;
	background-color: rgb(107, 114, 109);
	padding: 20px;
	position: relative;
	box-sizing: border-box;
}

.news div .n-left h3 {
	text-align: center;
}

.news div .n-left h1 {
	line-height: 50px;
}

.news div .n-left p {
	font-size: 17px;
	padding: 20px;
	box-sizing: border-box;
	line-height: 1.8;
}

.news div .n-left a {
	background-color: rgb(0, 4, 31);
	width: 100px;
	height: 30px;
	text-align: center;
	line-height: 29px;
	border: 5px;
	position: absolute;
	bottom: 50px;
	left: 40px;
	/* border-radius: 5px; */
	color: white;
}
.news div .n-right {
	width: 50%;
	height: 450px;
	background: url("/public/img/news.jpg") no-repeat center/cover;
}

.sorento-view {
	width: 100%;
	text-align: center;
}

.sorento-view h3 {
	font-size: 30px;
	margin: 0 auto;
}

.sorento-view p {
	width: 100%;
	margin-bottom: 50px;
	font-weight: 300;
}

/* 모바일 */

.m-home {
	width: 100%;
	height: auto;
	position: relative;
}

.m-home .mySwiper {
	width: 100%;
	height: 400px;
}

.m-home .slide {
	width: 100%;
	height: 100%;
	position: relative;
}

.m-home .slide p {
	position: absolute;
	left: 25px;
	bottom: 110px;
	text-shadow: 3px 3px 3px #333;
}

.m-home .slide h1 {
	position: absolute;
	left: 25px;
	bottom: 65px;
	text-shadow: 3px 3px 3px #333;
}

.m-home .slide a {
	position: absolute;
	left: 25px;
	bottom: 25px;
}

.m-home .slide img {
	object-fit: cover;
}

.m-home .best {
	width: 100%;
	position: relative;
	padding: 20px 0;
}

.m-home .best h3 {
	width: 100%;
	height: 40px;
	font-size: 25px;
	text-align: center;
}

.m-home .best p {
	font-size: 13px;
	text-align: center;
}

.m-home .sorento-view {
	width: 100%;
	height: auto;
	margin: 50px 0;
}

.m-home .sorento-view h3 {
	width: 100%;
	height: 50px;
}

.m-home .sorento-view .sorento-img {
	width: 100% !important;
}

.m-home .menu {
	width: 100%;
	height: auto;
	display: flex;
	flex-flow: column nowrap;
}

.m-home .menu .box {
	width: 90%;
	background-color: #ebebeb;
	margin: 10px auto;
	position: relative;
	cursor: pointer;
}

.m-home .menu .box:hover {
	background-color: rgb(0, 4, 31);
}

.m-home .menu .box span {
	position: absolute;
	bottom: 10px;
	right: 20px;
	transition: all 1s;
}

.m-home .menu .box:hover span {
	color: #fff;
}

.m-home .news {
	width: 90%;
	margin: 0 auto 50px;
	height: auto;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
	position: relative;
}

.m-home .news .t-news {
	width: 100%;
	height: auto;
}

.m-home .news .t-news img {
	width: 100%;
	height: 100%;
	object-fit: contain;
}

.m-home .news .t-news p {
	font-size: 20px;
	line-height: 25px;
	text-align: center;
	color: #fff;
	position: absolute;
	bottom: 50px;
	left: 10px;
}

.m-home .news .t-news span {
	width: 100px;
	height: 25px;
	background-color: #fff;
	position: absolute;
	bottom: 15px;
	left: 10px;
	color: black;
	cursor: pointer;
	text-align: center;
}
